@import './normalize.css';
@import './vars.css';
@import './fonts.css';

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    color: #ffffff;
    text-decoration: none;
    margin-right: 15px;
}

li {
    list-style: none;
}

img {
    display: block;
    width: 100%;
    height: auto;
}

button,
input {
    outline: none;
}

body {
    font-family: 'HelveticaNeueCyr', 'BauhausC', sans-serif;
}

.react-datepicker__header {
    background-color: var(--color-main) !important;
}

div>.react-datepicker__current-month,
div>.react-datepicker__day-name {
    color: #ffffff
}

.MuiAutocomplete-option {
    white-space: wrap;
    font-size: 12px;
    padding: 0;
    margin: 0;
    line-height: 1;
}

.MuiAutocomplete-listbox {
    width: 110%;
}

.acMainOption {
    font-weight: 700;
}
.react-datepicker-wrapper {
    width: 100%;
}

/* временные */

.wrapper {
    position: relative;
}

.title {
    position: absolute;
    top: 500px;
    left: 500px;
}

.disabled {
    display: none;
}

/* временные */